@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-font: "Roboto", sans-serif;
  --main-color: #2c2c2c;
  --button-color: #bbf330;
  --button-hover-color: #b5ff00;
  --subscribe-color: #ffe550;
  --border-color: #7eb101;
  --border-hover-color: #597d00;
  --warning-color: #ff705d;
  --bg-color: #fefefe;
  --animation-global: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p, ul, ol {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  flex-shrink: 0;
  display: block;
}

body {
  font-family: var(--main-font);
  color: var(--main-color);
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 16px;
  }
}

.nav {
  font-size: 16px;
  line-height: 1.19;
}

@media screen and (max-width: 767px) {
  .nav {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .nav {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 28px;
    margin-right: 28px;
    font-size: 14px;
    line-height: 1.14;
    display: flex;
  }
}

.nav__link {
  width: 100%;
  height: 100%;
  color: var(--main-color);
  transition: color var(--animation-global), font-weight var(--animation-global);
  padding: 16px;
  display: block;
}

@media screen and (min-width: 768px) {
  .nav__link {
    padding: 0;
  }
}

@media screen and (max-width: 767px) {
  .nav__link:hover, .nav__link:focus, .nav__link:active {
    background-color: var(--button-color);
  }
}

@media screen and (min-width: 768px) {
  .nav__link:hover, .nav__link:focus, .nav__link:active {
    color: var(--button-color);
    text-underline-offset: 2px;
    font-weight: 500;
    text-decoration: underline;
  }
}

@media screen and (min-width: 1280px) {
  .nav__link:hover, .nav__link:focus, .nav__link:active {
    font-weight: 700;
  }
}

@media screen and (max-width: 767px) {
  .nav.is-open {
    width: 130px;
    background-color: var(--bg-color);
    border-radius: 30px 0 30px 30px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    display: flex;
    position: absolute;
    top: 75%;
    right: 20px;
    overflow: hidden;
    box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
  }
}

.btn {
  color: var(--main-color);
  background-color: var(--button-color);
  cursor: pointer;
  border: none;
  border-radius: 30px;
  padding: 6px 18px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.54;
}

.btn:hover, .btn:focus {
  background-color: var(--button-hover-color);
}

