.swiper {
  width: 100%;
  height: 100%;
  border: 14px solid #ffe550;
  border-radius: 50%;
  position: static;
}

@media screen and (min-width: 1280px) {
  .swiper {
    border: 26px solid #ffe550;
  }
}

.swiper-slide {
  text-align: center;
  background: #fff;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  display: flex;
}

.swiper-slide__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.swiper .swiper-button-next {
  z-index: 10;
  position: absolute;
  right: -10px;
}

.swiper .swiper-button-prev {
  z-index: 10;
  position: absolute;
  left: -10px;
}

@media screen and (min-width: 768px) {
  .swiper .swiper-button-prev, .swiper .swiper-button-next {
    width: 34px;
    height: 28px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
  }
}

@media screen and (min-width: 1280px) {
  .swiper .swiper-button-prev, .swiper .swiper-button-next {
    width: 48px;
    height: 40px;
  }
}

.swiper .swiper-button-disabled {
  opacity: 1;
}

.swiper .swiper-button-prev:active, .swiper .swiper-button-next:active {
  box-shadow: 0 4px 4px rgba(0, 0, 0, .16);
}

.swiper .swiper-button-next:after, .swiper .swiper-button-prev:after {
  color: var(--main-color, #2c2c2c);
  font-size: 24px;
  font-weight: 600;
}

@media screen and (min-width: 1280px) {
  .swiper .swiper-button-next:after, .swiper .swiper-button-prev:after {
    font-size: 30px;
  }
}

.swiper .swiper-button-next:hover:after, .swiper .swiper-button-prev:hover:after {
  color: var(--border-color, #7eb101);
}

